import React from 'react';
import xebia_logo from './xebia_logo.png';
import {Link} from 'react-router-dom';

const LoginView = ({getUserName, getPassWord, validateCredentials})=>{
    
    const handleChangeUserName = (e) => {
       getUserName(e.target.value);
    }

    const handleChangePassword= (e)=> {
        getPassWord(e.target.value);
    }

    const handleSubmit =(e) => {
        e.preventDefault();
        console.log("hello")
        validateCredentials();
        
    }
    
    return( <div className="background-container">
    <div className="container">
        <div className="form-wrapper">
            <div className="container-brand-wrapper">
                <img src={xebia_logo} alt="nothing going on here" />
            </div>
            <div className="vertical-line"></div>
                <form onSubmit={handleSubmit}>

                    <div className="heading-wrapper">
                        <span id="sign-in-heading">
                            Sign in
                        </span>

                        <span id="text-wrapper">
                            to access HDesk
                        </span>
                    </div>               
                    
                                
                    <div className="input-wrapper">
                        <div className="email-input-wrapper">            
                            <input 
                            type="text" 
                            placeholder="Username"
                            id="username"
                            onChange={handleChangeUserName}
                            />
                        </div>    

                        <div className="password-input-wrapper">
                            <input
                            type="text"
                            id="password"
                            placeholder="Password"
                            onChange={handleChangePassword}
                            />
                        </div>
                        <div className="button-wrapper">
                        {/* <Link to='/TicketList'>   */}
                            <input type="submit" id= "login-form-submit" value="Submit" />
                        {/* </Link> */}
                        </div>
                    </div>
                </form>
            </div>
        </div>
    </div>
)
}
export default LoginView;