import React, {Component} from 'react';
import LoginView from '../Views/LoginView';
// import xebia_logo from '../Views/xebia_logo.png';
// import {Link} from 'react-router-dom';
import axios from 'axios';
class Login extends Component{

    constructor(props){
        super(props);
        this.state={
            username: '',
            password: ''
        }
    }
    

  getUserName= (username)=>{
        this.setState(
            {username: username}
        )
    }

   getPassWord = (password)=>{
        this.setState({
            password : password
        })
        //console.log(this.state);
    }

    validateCredentials= () => {
        console.log("hello");
        let postData = {
            email: this.state.username,
            password: this.state.password
        }
        
        let autoConfig={
            headers: {
                'x-access-channel' : 'WEB',
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'GET, POST, PUT, PATCH, POST, DELETE, OPTIONS',
                'Access-Control-Allow-Headers': 'Content-Type',
                'Access-Control-Max-Age': 86400
            }
        };
        axios.post('http://localhost:8080/ticket-tool/v1/users/login',postData, autoConfig)

        .then( (response) => {
            // console.log(response);
            this.props.history.push('/TicketList');
            window.localStorage.setItem('Token',response.data.token);
            window.localStorage.getItem('Token')
        })
        .catch((error)=>{
            console.log(error);
            alert('please enter the correct credentials');
        })
        
    }
    render(){
        return(
           <LoginView getUserName={this.getUserName} getPassWord={this.getPassWord} validateCredentials={this.validateCredentials} />
        )
    }
}

export default Login;