import React,{Component} from 'react';
import TicketListNav from '../Views/TicketListNav';
import TicketListColumn from '../Views/TicketListColumn';
import axios from 'axios';


class TicketList extends Component{

    constructor(props){
        super(props);

        this.state={
            post: null
        }
    }

    componentDidMount(){
        // console.log(window.localStorage.getItem('Token'));
        const token = window.localStorage.getItem('Token');
        const headers ={
            'Authorization' : 'Bearer' + token
        }
    //    console.log(headers)
        axios.get('ticket-tool/v1/tickets?page=0&limit=2', headers)
        .then(response => {
            console.log(response);
        })
        .catch(error=>{
            console.log(error)
        })
    }
    render(){
        return(
            <div className="TicketListContainer">
                <TicketListNav />
                <div className="bottom-container">
                    <TicketListColumn />
                    <div className="ticket-container">
                        <div className="ticket-container-nav">
                            <div className="select-all-checkbox">
                                <input type="checkbox" />
                            </div>
                            <div className="headings-wrapper">
                                <div className="ticket-id-wrapper">
                                    Ticket ID
                                </div>
                                <div className="subject-wrapper">
                                    Subject
                                </div>
                                <div className="contact-name-wrapper">
                                    Contact Name
                                </div>
                                <div className="customer-responded-time-wrapper">
                                    Customer Responded Time
                                </div>
                                <div className="Due-date-wrapper">
                                    Due Date
                                </div>
                                <div className="Status-wrapper">
                                    Status
                                </div>
                                <div className="Ticket-owner-wrapper">
                                    Ticket Owner
                                </div>
                                <div className="Channel-wrapper">
                                    Channel
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        )
    }
}

export default TicketList;